import axios from "axios";
import { useState } from "react";
import { useQuery } from "react-query";
import styled from "styled-components";

import NumberFormat from "react-number-format";

import Text from "components/Text";
import Input from "components/Input";
import Section from "components/Section";

export default function App(){

    const [dollarAmount, setDollarAmount] = useState(5);

    const { isLoading, data, error } = useQuery('dollarPrice', () => (
        axios.get("https://api-divisas-ve.herokuapp.com/v1/dollar")
    ));

    const dollarPrice = data?.data?.data?.value;

    if( isLoading ) return (
        <Center>
            <Text center>Obteniendo datos, por favor espere</Text>
        </Center>
    );

    if( error ) return (
        <Center>
            <Text center>Ha ocurrido un error al intentar obtener los datos: <Text as="strong">{error.message}</Text></Text>
        </Center>
    );

    return (<>
        <Section>
            <Text as="h2" center>Precio actual del dólar</Text>
            <Text center as="h3" size={2.5} color="green" regular>
                <NumberFormat
                    prefix="Bs. "
                    decimalScale={2}
                    fixedDecimalScale
                    value={dollarPrice}
                    displayType="text"
                    decimalSeparator=","
                    thousandSeparator="."
                    allowEmptyFormatting
                />
            </Text>
            <Text center light size={.8} style={{ fontStyle: "italic" }}>
                Precio obtenido de la página oficial del &nbsp;
                <a href="http://bcv.org.ve">Banco Central de Venezuela</a>
            </Text>
        </Section>
        <Section>
            <Text as="h2" center>Calculadora</Text>
            <div
                style={{
                    gap: "8px 0",
                    display: "grid",
                    gridTemplateColumns: "45% 10% 45%"
                }}
            >
                <label style={{ flex: 1 }} htmlFor="dollarAmount">
                    <Text medium>Dólares</Text>
                </label>
                <div></div>
                <label style={{ flex: 1 }}>
                    <Text medium>Bolívares</Text>
                </label>
                <NumberFormat
                    prefix="$ "
                    id="dollarAmount"
                    autoComplete="off"
                    customInput={Input}
                    value={dollarAmount}
                    decimalSeparator=","
                    thousandSeparator="."
                    allowEmptyFormatting
                    onValueChange={e => setDollarAmount(e.floatValue)}
                />
                <i
                    className="fas fa-equals"
                    style={{
                        opacity: .5,
                        alignSelf: "center",
                        justifySelf: "center",
                    }}
                />
                <NumberFormat
                    disabled
                    prefix="Bs. "
                    decimalScale={2}
                    fixedDecimalScale
                    customInput={Input}
                    decimalSeparator=","
                    thousandSeparator="."
                    allowEmptyFormatting
                    value={dollarAmount ? (dollarAmount * dollarPrice) : dollarPrice}
                />
            </div>
        </Section>
        <Section>
            <Text as="h2" center>Información del sitio</Text>
            <Text>
                &nbsp;&nbsp;&nbsp;&nbsp;
                Este sitio web ha sido realizado por los estudiantes de Informática Trayecto IV de la UPTA Gabriel Mejias (27.536.202) y Belén Vivas (28.024.178) con la finalidad de cumplir con la actividad asignada para Bases de Datos por el profesor José Martínez.
            </Text>
            <br />
            <Text>Proveedor de datos:</Text>
            <a href="https://api-divisas-ve-web.pages.dev">https://api-divisas-ve-web.pages.dev</a>
        </Section>
        <Footer>
            <div>
                <Text center>
                    &copy;&nbsp; Calculadora USD/BS - Copyright 2021
                </Text>
            </div>
        </Footer>
    </>);

}

const Footer = styled.footer`

    width: 100%;
    margin-top: 7rem;
    padding: 2rem 0 2rem 0;
    background-color: gainsboro;

    & div {
        margin: 0 auto;
        padding: 0 1rem;
        max-width: 500px;
    }

`;

const Center = styled.div`
    width: 100%;
    height: 100vh;
    display: flex;
    padding: 0 5rem;
    align-items: center;
    justify-content: center;
`;