import styled, { css } from "styled-components";

export default styled.input`

    outline: 0;
    padding: .5rem;
    font-size: 1rem;
    border-radius: 5px;
    border: 2px solid gray;
    transition: border .2s ease;

    &:focus { border-color: steelblue }

    ${props => props.disabled && css`
        color: black;
        background: #eee;
        border-color: lightgray;
    `}

`;