import styled from "styled-components";

const Text = styled.p`

    font-weight: ${props => {
        switch(true){
            case props.regular: return 400;
            case props.medium: return 500;
            case props.light: return 300;
            case props.bold: return 600;
        }
    }};

    text-align: ${props => {
        switch(true){
            case props.right: return "right";
            case props.center: return "center";
            default: return "left";
        }
    }};

    color: ${props => props.color};
    font-size: ${props => props.size}rem;

`;

export default Text;